import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { Avatar, Box, CardHeader, Container, ListItem, ListItemAvatar, ListItemText, Typography } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import EmailIcon from '@mui/icons-material/Email';

import { Locations } from '../utils/constants';
import parse from 'html-react-parser'
import graphic from '../images/iCode_Graphic.png';

const styles = {
    paperContainer: {
        borderRadius: '0 0 4px 4px'
    }
};
const AppHero = () => {
    return (
        <Container maxWidth="xl" disableGutters>
            <Card style={styles.paperContainer}>
                <CardMedia
                    sx={{ height: 300 }}
                    image={graphic}
                    title="iCode STEM Programs & Camps"
                />
                <CardContent>
                <Typography variant='h4' sx={{marginBottom:2, textAlign: 'center'}}>Find the iCode location nearest you:</Typography>
                <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)' }}>
                    {Locations.map((page) => (
                        <Card key={page.title} sx={{marginLeft: 1, marginRight: 1}}>
                            <CardHeader title={page.title} sx={{textAlign: 'center'}}></CardHeader>
                            <CardContent>
                                <ListItem>
                                    <ListItemAvatar>
                                    <Avatar>
                                        <LocalPhoneIcon />
                                    </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={page.phone} />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                    <Avatar>
                                        <EmailIcon />
                                    </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={page.email}/>
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                    <Avatar>
                                        <HomeIcon />
                                    </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={page.address1} secondary={page.address2} />
                                </ListItem>                                
                                {parse(page.map)}
                            </CardContent>
                        </Card>
                    ))}
                </Box>
                </CardContent>
            </Card>
        </Container>
    )
}

export default AppHero;